import React from 'react';
import SkillCard from './SkillCard';

const SkillsContainer = () => {
  const reactPwaLogos = [`${process.env.PUBLIC_URL}/images/react.svg`];
  const reactPwaDesc = "Font End Apps & PWA's Using React";

  const reactMernLogos = [
    `${process.env.PUBLIC_URL}/images/react.svg`,
    `${process.env.PUBLIC_URL}/images/node.svg`,
    `${process.env.PUBLIC_URL}/images/mongo.svg`,
  ];
  const reactMernDesc =
    'Full MERN Stack Web Development [MongoDB, ExpressJS, React, NodeJS]';

  const jhcLogos = [`${process.env.PUBLIC_URL}/images/logo-js-html-css.png`];
  const jhcDesc = (
    <p>
      Web development using pure <span className='text-red'>HTML5</span>,{' '}
      <span className='text-blue'>CSS3</span>, and{' '}
      <span className='text-yellow'>Javascript</span>
    </p>
  );

  const dotNetLogos = [`${process.env.PUBLIC_URL}/images/logo-dot-net.png`];
  const dotNetDesc =
    'Software C# & WPF Tooling Engineer for 6 years @ Valeo Automotive Systems';

  const embeddedLogos = [`${process.env.PUBLIC_URL}/images/logo-embedded.png`];
  const embeddedDesc =
    'Embedded Software Engineer for 4 Years @ Valeo Automotive Systems';

  return (
    <div className='container'>
      <div className='row'>
        <SkillCard
          logos={reactPwaLogos}
          desc={reactPwaDesc}
          link='/m-faried-portfolio/pwa-react'
        />
        <SkillCard
          logos={reactMernLogos}
          desc={reactMernDesc}
          link='/m-faried-portfolio/mern-stack'
        />
        <SkillCard
          logos={jhcLogos}
          desc={jhcDesc}
          link={'/m-faried-portfolio/jhc'}
        />
      </div>
      <div className='row justify-content-center'>
        <SkillCard
          logos={dotNetLogos}
          desc={dotNetDesc}
          link='/m-faried-portfolio/dot-net'
        />
        <SkillCard
          logos={embeddedLogos}
          desc={embeddedDesc}
          link='/m-faried-portfolio/embedded'
        />
      </div>
    </div>
  );
};

export default SkillsContainer;
