import React from 'react'
import Project from '../layout/Project';

const PwaReact = () => {

    const kindleHighlights = {
        title: 'K-Highlights',
        descriptions: ['K-Highlights is a viewer for Kindle highlights and clippings. The application is deployed as a website using HEROKU and published on PWA store.'],
        screenShots: [
            `${process.env.PUBLIC_URL}/images/k-highlights.PNG`,
        ],
        tecnologies: [
            'REACT.',
            'Workbox for creating Service Worker and handling caching resources.',
            'Bootstrap.'
        ],

        links: [
            { header: 'Deployed Website', ref: 'https://k-highlights.herokuapp.com/', display: 'K-Highlights' },
            { header: 'PWA Store Link', ref: 'https://progressiveapp.store/pwa/K-Highlights', display: 'K-Highlights' },
        ]
    }

    const githubFinder = {
        title: 'Github Finder',
        descriptions: ['Github Finder is an application which helps users to search Github developer profiles.'],
        screenShots: [
            `${process.env.PUBLIC_URL}/images/GitHubFinder.PNG`,
        ],
        tecnologies: [
            'REACT.',
            'Github API'
        ],
        links: [
            { header: 'Deployed Website', ref: 'https://m-faried.github.io/github-finder/', display: 'Github Finder' },
        ]
    }


    return (
        <div className="container my-5">
            <Project {...kindleHighlights} />
            <Project {...githubFinder} />
        </div>
    )
}

export default PwaReact;
