import React from 'react'
import Project from '../layout/Project';

const Jhc = () => {

    const mealRecipeFinder = {
        title: 'Meal Recipe Finder',
        descriptions: ['Meal Recipe Finder is an application which helps users find meal recipies to cook.'],
        screenShots: [
            `${process.env.PUBLIC_URL}/images/MealFinder_01.PNG`,
            `${process.env.PUBLIC_URL}/images/MealFinder_02.PNG`,
        ],
        tecnologies: [
            'Plain HTML5, CSS3, and Javascript.',
            'The Meal DB Web Service.'
        ],
        links: [
            { header: 'Deployed Website', ref: 'https://m-faried.github.io/meal-recipe-finder/', display: 'Meal Recipe Finder' },
        ]
    }

    const myVoice = {
        title: 'My Voice',
        descriptions: ['My Voice is an application to help autistic children communicate their thoughts and needs.'],
        screenShots: [
            `${process.env.PUBLIC_URL}/images/my-voice.PNG`
        ],
        tecnologies: [
            'Plain HTML5, CSS3, and Javascript.',
            'Web Speech API.'
        ],
        links: [
            { header: 'Deployed Website', ref: 'https://m-faried.github.io/my-voice/', display: 'My Voice' },
        ]
    }

    const hangman = {
        title: 'Hangman Game',
        descriptions: ['An online hangman game.'],
        screenShots: [
            `${process.env.PUBLIC_URL}/images/hangman.PNG`
        ],
        tecnologies: [
            'Plain HTML5, CSS3, and Javascript.'
        ],
        links: [
            { header: 'Deployed Website', ref: 'https://m-faried.github.io/hangman-game/', display: 'Hangman Game' },
        ]
    }

    const exchangeRate = {
        title: 'Exchange Rate Calculator',
        descriptions: ['An online hangman game.Currency exchange rate calculator application.'],
        screenShots: [
            `${process.env.PUBLIC_URL}/images/ExchangeRateCalculator.PNG`
        ],
        tecnologies: [
            'Plain HTML5, CSS3, and Javascript.'
        ],
        links: [
            { header: 'Deployed Website', ref: 'https://m-faried.github.io/exchange-rate-calculator/', display: 'Exchange Rate Calculator' },
        ]
    }

    const lyricsSearch = {
        title: 'Song Lyrics Search App',
        descriptions: ['Lyrics search application.'],
        screenShots: [
            `${process.env.PUBLIC_URL}/images/LyricsSearchApp.PNG`
        ],
        tecnologies: [
            'Plain HTML5, CSS3, and Javascript.'
        ],
        links: [
            { header: 'Deployed Website', ref: 'https://m-faried.github.io/lyrics-search-app/', display: 'Lyrics Search App' },
        ]
    }

    const relaxer = {
        title: 'Relaxer',
        descriptions: ['A web application to guide users practice relaxing breathing technique.'],
        screenShots: [
            `${process.env.PUBLIC_URL}/images/Relaxer.PNG`
        ],
        tecnologies: [
            'Plain HTML5, CSS3, and Javascript.'
        ],
        links: [
            { header: 'Deployed Website', ref: 'https://m-faried.github.io/relaxer/', display: 'Relaxer' },
        ]
    }


    return (
        <div className="container my-5">
            <Project {...mealRecipeFinder} />
            <Project {...myVoice} />
            <Project {...hangman} />
            <Project {...exchangeRate} />
            <Project {...lyricsSearch} />
            <Project {...relaxer} />
        </div>
    )
}

export default Jhc;
