import React from 'react'
import Project from '../layout/Project';

const MernStack = () => {

    const contactKeeper = {
        title: 'Contact Keeper',
        descriptions: ["Contact Keeper is a full stack MERN application which allows users to create accounts and Create, Retrieve, Update, Search, Delete their contacts.", "The front-end was built using REACT, and the back-end was implemented as a web API and all the communication with the database is made using HTTP requests and authenticated using using JsonWebTokens.", " I used the MongoDB cloud version for this project and the connection to database and Model Object management was done using Mongoose framework."],
        screenShots: [
            `${process.env.PUBLIC_URL}/images/ContactKeeper_01.PNG`,
            `${process.env.PUBLIC_URL}/images/ContactKeeper_02.PNG`,
        ],
        tecnologies: [
            "REACT for front-end.",
            "Node.js & ExpressJS for creating the back-end API which controls the CRUD operations of the data and handles user authentication and session.",
            "Json Web Tokens[jwt] to manage user session and authentication using tokens.",
            "Mongoose module to create the database access layer and object data modeling.",
            "Cloud version of MongoDB which was created using Atlas."
        ],
        links: [
            { header: 'Deployed Website', ref: 'https://contact-keeper-11.herokuapp.com', display: 'Contact Keeper' },
        ]
    }

    const expenseTracker = {
        title: 'Expense Tracker',
        descriptions: ["Expense Tracker is a full stack MERN application which allows users to create accounts and track expenses versus their income.", "The front-end was built using REACT, and the back-end was implemented as a web API and all the communication with the database is made using HTTP requests and authenticated using using JsonWebTokens.", "I used the MongoDB cloud version for this project and the connection to database and Model Object management was done using Mongoose framework."],
        screenShots: [
            `${process.env.PUBLIC_URL}/images/ExpenseTracker_01.PNG`,
            `${process.env.PUBLIC_URL}/images/ExpenseTracker_02.PNG`,
        ],
        tecnologies: [
            "REACT for front-end.",
            "Node.js & ExpressJS for creating the back-end API which controls the CRUD operations of the data and handles user authentication and session.",
            "Json Web Tokens[jwt] to manage user session and authentication using tokens.",
            "Mongoose module to create the database access layer and object data modeling.",
            "Cloud version of MongoDB which was created using Atlas."
        ],
        links: [
            { header: 'Deployed Website', ref: 'https://expense-tracker-11.herokuapp.com', display: 'Expense Tracker' },
        ]
    }


    return (
        <div className="container my-5">
            <Project {...contactKeeper} />
            <Project {...expenseTracker} />
        </div>
    )
}

export default MernStack;
