import React from 'react';
import './Intro.css';

const Intro = () => {
  const logogSrc = process.env.PUBLIC_URL + '/images/logo.jpg';
  // const backgroundURL = process.env.PUBLIC_URL + '/images/react.svg';

  return (
    <div className='container'>
      <div className='intro-container'>
        {/* <div className="bg-img" style={{ backgroundImage: `url(${backgroundURL})` }}></div> */}

        <div className='logo popup-element pop'>
          <img src={logogSrc} alt='' />
        </div>

        <div className='intro-text'>
          <h2 className='popup-element pop'>
            He<span className='text-blue'>ll</span>o,
          </h2>

          <h2 className='popup-element pop'>
            <span className='text-blue'>I</span> am
          </h2>

          <h1 className='popup-element pop'>
            M<span className='text-blue'>.</span>FAR
            <span className='text-blue'>I</span>ED
          </h1>

          <small className='popup-element pop text-blue'>
            Full Stack Web Developer / Desktop Developer / Freelancer
          </small>
        </div>
      </div>
    </div>
  );
};

export default Intro;
