import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';

import './Navbar.css';

const Navbar = () => {
  const [open, setOpen] = useState(true);

  const linkedInIcon = `${process.env.PUBLIC_URL}/images/linkedin.png`;

  return (
    <nav className={open ? 'show' : ''}>
      <div className='nav-container'>
        <NavLink to='/m-faried-portfolio'>
          <i className='fas fa-home fa-2x'></i>
        </NavLink>

        <NavLink to='/m-faried-portfolio/contacts'>
          <i className='fas fa-address-card fa-2x'></i>
        </NavLink>

        <a
          href='https://www.linkedin.com/in/mohamed-faried-0258a445/'
          target='blank'
        >
          <img src={linkedInIcon} alt='Linked In' />
        </a>
      </div>

      <button className='menu-btn' onClick={() => setOpen(!open)}>
        <i
          className={
            open ? 'fas fa-chevron-circle-left' : 'fas fa-chevron-circle-right'
          }
        ></i>
      </button>
    </nav>
  );
};

export default Navbar;
